.skill-list {
  display: flex;
  flex-wrap: wrap;
  gap: 24px 0;
  margin-top: 24px;
  background-image: url('../img/Skils.png');
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center center;
  background-attachment: fixed;
  height: 60vh;
  overflow: scroll;
}

.skill-item {
  display: flex;
  width: 48%;
  gap: 36px;
}

.skill-img {
  width: 20%;
  /* background-color: rgba(255, 255, 255, 20%); */
  display: flex;
  align-items: center;
}

.skill-img img {
  width: 100%;
  object-fit: cover;
}

.skill-body {
  width: 70%;
}

@media screen and (max-width:768px) {
  .skill-list {
    background-size: 100%;
  }

  .skill-item {
    width: 100%;
  }
}