html {
  font-size: 14px;
}

.container {
  max-width: 960px;
  width: 960px;
  margin: 0 auto;
  padding: 0 12px;
}

header {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-image: url("img/rassen.png");
}

.home {
  width: 10%;
  margin: 0;
  display: flex;
  align-items: center;
}

.home a {
  color: #333;
  display: flex;
  align-items: center;
  padding: 12px;
}

.home img {
  width: 100%;
}

header nav {
  font-size: 24px;
}

nav ul {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.header-menu li + li {
  margin-left: 24px;
}

.header-menu li a {
  color: #fff;
  transition: color 0.3s;
}

.header-menu li a:hover {
  color: rgb(244, 110, 202);
}

.hmenu {
  position: fixed;
  z-index: 11;
}

.hmenu ul {
  position: fixed;
  inset: -100vh auto auto 20%;
  transition: inset 0.4s ease-in;
  font-size: 32px;
  padding: 0;
}

.hmenu ul li a {
  color: #fff;
}

.main-visual {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
}

.main-visual img {
  width: 100%;
}

.mainTitle {
  color: #fff;
  text-shadow: 2px 2px 5px blue;
  font-size: 4rem;
  transform: translate(10%, -100%);
  @media screen and (max-width: 768px) {
    transform: translateY(0%);
    writing-mode: vertical-lr;
    text-orientation: upright;
    font-size: 3rem;
  }
}

h2 {
  font-size: 36px;
  border-left: 5px solid #00bfff;
  padding-left: 12px;
  margin: 12px 0 0;
}

.tab-container {
  display: flex;
  max-width: 960px;
  margin: 16px auto 0;
  position: relative;
}

.tab-container button {
  flex: 1 1 0;
  padding: 16px 0;
  border: 1px solid #00bfff;
  border-radius: 8px 8px 0 0;
  background-color: rgba(255, 255, 255, 20%);
  font-size: 16px;
  cursor: pointer;
}

.tab-container button.active {
  background-image: linear-gradient(45deg, #87ceed, #00bfff, #87cefa);
  font-size: 24px;
  font-weight: bold;
  color: #fff;
}

.tabBorder {
  width: 33.3%;
  height: 3px;
  background-color: rgb(244, 110, 202);
  position: absolute;
  bottom: 0;
  transition: left 0.3s ease-in;
}

.copy {
  text-align: center;
  font-size: 12px;
}

@media screen and (max-width: 768px) {
  header {
    justify-content: flex-start;
  }

  .home {
    width: 20%;
    margin: 16px;
  }

  header nav {
    display: none;
  }

  /* ハンバーガーメニュー */

  .hmenuactive {
    width: 100vw;
    height: 100vh;
    position: fixed;
    inset: 0 auto auto 0;
    z-index: 2;
    padding: 0;
    margin: 0;
    background-color: rgba(0, 166, 255, 40%);
    transition: background-color 0.4s ease-out;
  }

  .hmenuactive ul {
    transition: inset 0.4s ease-in;
    position: fixed;
    inset: 40% auto auto 30%;
    padding: 0;
    font-size: 32px;
  }

  .hmenuactive ul li a {
    color: #fff;
  }

  .hmenuactive ul li {
    display: flex;
    align-items: center;
  }

  .hmenuactive ul li + li {
    margin: 16px 0 0;
  }

  .hmbtn {
    width: 50px;
    height: 50px;
    display: grid;
    place-items: center;
    background-color: #00bfff;
    position: fixed;
    inset: auto auto 3% 45%;
    border-radius: 50%;
    z-index: 9999;
  }

  .hmbtn span {
    background-color: #fff;
    height: 3px;
    width: 70%;
    grid-area: 1/1/-1/-1;
    border-radius: 16px;
    transition: transform 0.4s ease-in, opacity 0.4s ease-in;
  }

  .hmbtn span:nth-child(1) {
    transform: translateY(-10px);
  }

  .hmbtn span:nth-child(3) {
    transform: translateY(10px);
  }

  .hmbtn .active:nth-child(1) {
    transform: translateY(0) rotate(135deg);
  }

  .hmbtn .active:nth-child(2) {
    transform: translateX(100px);
    opacity: 0;
  }

  .hmbtn .active:nth-child(3) {
    transform: translateY(0) rotate(-135deg);
  }
}
